html {
    background-color: #17171d;
}

iframe {
    width: calc(100% - 40px);
    height: 321px;
    position: absolute;
    bottom: 0;
    left: 0;
    border: 0;
    margin: 20px
}

.bottom-button {
    margin-top: 12px !important;
}


#full-screen {
    position: absolute;
    bottom: 340px;
    left: 0;
    border: 0;
    margin: 20px;
    color: rgb(186, 191, 196)
}

.s-container {
    border-radius: 15px;
    border: 1px solid #31366d;
    width: fit-content;
    display: inline-block;
    margin: 12px;
    padding: 15px 15px 3px;
}


@media (max-height: 690px) and (min-width: 1111px) {

    iframe {
        position: relative;
        bottom: unset;
        margin: 20px;
    }


    #full-screen {
        position: relative;
        bottom: unset;
        margin-top: 20px;
        margin-bottom: 0;
    }

}

